<template>
  <div id="LeagueProfile">
    <div class="container-custom">
      <div class="row" v-if="leagueAccount != null">
        <div class="col-2">
          <div class="row">
            <div class="col">
              <p>{{leagueAccount.iconUrl}}</p>

              <img class="avatar" />
              <img class="avatar" :src="leagueAccount.iconUrl" />
              <h2>{{leagueName}}</h2>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <h2>Level: {{leagueAccount.summonerLevel}}</h2>
            </div>
          </div>
        </div>
        <div class="col-10">
          <ul>
            <li v-for="match in leagueAccount.matches.slice(0,10)" :key="match.gameId">{{match}}</li>
          </ul>
        </div>
      </div>

      <p></p>
    </div>
  </div>
</template>

<script>
export default {
  name: "LeagueProfile",
  data() {
    return {
      leagueAccount: null,
      iconUrl: "",

      api_url: "http://localhost:4000/",
      namePretty: ""
    };
  },

  props: ["leagueName"],

  mounted() {
    // eslint-disable-next-line no-console
    console.log(this);
    if (this.leagueName != "") {
      this.namePretty = this.leagueName;
      document.title = this.namePretty;
      fetch(this.api_url + "leagueoflegends/user/" + this.leagueName)
        .then(response => response.json())
        .then(result => {
          this.leagueAccount = result;
          this.leagueAccount.iconUrl =
            "../../static/icons/" + this.leagueAccount.profileIconId + ".png";
        });
    }
  },
  methods: {}
};
</script>
<style>
.container-custom {
  padding-right: 25px;
  padding-left: 25px;
  padding-bottom: 100px;
}
.avatar {
  vertical-align: middle;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.table {
  color: white;
}
.table thead {
  font-weight: bold;
  font-size: 16px;
}

#OsrsProfile {
  background-color: black;
}
</style>
